.za-popup {
  position: relative; }
  .za-popup-wrapper {
    position: fixed;
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
    -webkit-transition-property: -webkit-transform;
    transition-property: -webkit-transform;
    transition-property: transform;
    transition-property: transform, -webkit-transform;
    overflow: auto;
    -webkit-overflow-scrolling: touch;
    z-index: 1300; }
    .za-popup-wrapper-bottom {
      left: 0;
      bottom: 0;
      width: 100%; }
    .za-popup-wrapper-top {
      left: 0;
      top: 0;
      width: 100%; }
    .za-popup-wrapper-left {
      left: 0;
      top: 0;
      height: 100%; }
    .za-popup-wrapper-right {
      right: 0;
      top: 0;
      height: 100%; }
  .za-popup-hidden .za-popup-wrapper-bottom {
    -webkit-transform: translate(0, 100%);
            transform: translate(0, 100%); }
  .za-popup-hidden .za-popup-wrapper-top {
    -webkit-transform: translate(0, -100%);
            transform: translate(0, -100%); }
  .za-popup-hidden .za-popup-wrapper-left {
    -webkit-transform: translate(-100%, 0);
            transform: translate(-100%, 0); }
  .za-popup-hidden .za-popup-wrapper-right {
    -webkit-transform: translate(100%, 0);
            transform: translate(100%, 0); }

.za-accordion-item-title {
  position: relative;
  height: 45px;
  line-height: 45px;
  padding: 0 15px; }
  .za-accordion-item-title:after {
    content: '';
    pointer-events: none;
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    border-radius: 0;
    border-bottom: 1px solid #ddd; }
    @media only screen and (-webkit-min-device-pixel-ratio: 2) {
      .za-accordion-item-title:after {
        width: 200%;
        height: 200%;
        -webkit-transform: scale(0.5, 0.5);
                transform: scale(0.5, 0.5);
        -webkit-transform-origin: 0 0;
                transform-origin: 0 0; } }

.za-accordion-item-content {
  position: relative;
  height: 0;
  overflow: hidden; }
  .za-accordion-item-content:after {
    content: '';
    pointer-events: none;
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    border-radius: 0;
    border-bottom: 1px solid #ddd; }
    @media only screen and (-webkit-min-device-pixel-ratio: 2) {
      .za-accordion-item-content:after {
        width: 200%;
        height: 200%;
        -webkit-transform: scale(0.5, 0.5);
                transform: scale(0.5, 0.5);
        -webkit-transform-origin: 0 0;
                transform-origin: 0 0; } }
  .za-accordion-item-content-inner {
    padding: 9px 15px; }

.za-accordion-item-content-anim {
  -webkit-transition: all 0.15s ease-out;
  transition: all 0.15s ease-out; }

.za-accordion-item-arrow {
  position: absolute;
  width: 10px;
  height: 10px;
  top: 17px;
  right: 15px;
  border-right: 2px solid #c7c7cc;
  border-bottom: 2px solid #c7c7cc;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  -webkit-transition: -webkit-transform 0.15s ease-out;
  transition: -webkit-transform 0.15s ease-out;
  transition: transform 0.15s ease-out;
  transition: transform 0.15s ease-out, -webkit-transform 0.15s ease-out; }
  .za-accordion-item-arrow-hidden {
    visibility: hidden; }

.za-accordion-item.active .za-accordion-item-content {
  height: auto; }

.za-accordion-item.active .za-accordion-item-arrow {
  -webkit-transform: rotate(-135deg);
          transform: rotate(-135deg); }

@media (-webkit-min-device-pixel-ratio: 3), (min-resolution: 3dppx) {
  .za-accordion-item-title:after, .za-accordion-item-content:after {
    width: 200%;
    height: 200%;
    -webkit-transform: scale(0.5);
            transform: scale(0.5); } }
